@import url("https://fonts.googleapis.com/css?family=Montserrat");

.header {
    align-items: center;
    margin-bottom: 0px;
    background-color: #1B1E21;

    .title {
        font-size: 50px;
        font-family: 'Montserrat', sans-serif;
        text-align: center;
    }

    .row.top-row {
        flex-wrap: nowrap;
    }

    .nav-link {
        color: #13A2E2;
        text-decoration: none;
        font-weight: bold;

        &:hover {
            border-color: #F2F2F2;
            color: #F2F2F2;
        }
    }

    .navbar {
        flex-direction: column;
        padding-bottom: 0px;

        .navbar-toggler {
            margin-bottom: 15px;
        }

        .navbar-nav {
                justify-content: space-evenly;

            .nav-item {
                font-size: 20px;
                text-align: center;
                transition: all 0.5s;
                padding-left: 25px;
                padding-right: 25px;
                border-bottom: solid 1px transparent;
                color: #13A2E2;
                text-decoration: none;

                &.active {
                    //border-color: #13A2E2;
                    color: #13A2E2;
                }

                &:hover {
                    border-color: #F2F2F2;
                    color: #F2F2F2;
                }
            }
        }
    }
}