@import url("https://fonts.googleapis.com/css?family=Roboto");

.project-text-card {
    background-color: #16181b;
    border: solid 1px rgb(56, 53, 49);
    padding: 10px;
    padding-top: 20px;
    border-radius: 5px;
    margin-bottom: 5px;
    margin-top: 5px;
    
    h3, h4, li {
        margin-left: 20px;
        margin-right: 20px;
        font-size: 18px;
        font-style: 'Roboto', 'sans-serif';
    }

    h2 {
        font-weight: bold;
        color: #13A2E2;
    }

    h5 {
        font-style: 'Roboto', 'sans-serif';
    }

    h4 {
        font-size: 24px;
        color: #13A2E2;
    }

    h2, h5 {
        text-align: center;
    }

    p, h3, h5, li {
        color: #d8d8d8;
    }

    &.centered {
        text-align: center;
    }

    a {
        color: #d8d8d8;
        text-decoration: none;
        text-align: center;
        width: 20%;
        h3:hover {
            color: #13A2E2;
        }
    }
}