@import url("https://fonts.googleapis.com/css?family=Lato");

.projects {
    margin-left: 40px;
    margin-right: 40px;
    padding-bottom: 40px;
    
    .project-cards {
        display: grid;
        grid-gap: 1.5rem;
        grid-auto-rows: 1fr;
        grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
        
        .row {
            --bs-gutter-y: 1.5rem;
        }
    }
    
    hr {
        margin-top: 1rem;
        margin-bottom: 1rem;
        border: 0;
        border-top: 2px solid #13A2E2;
    }

    h2 {
        margin-top: 40px;
        font-family: 'Lato', sans-serif;
        font-weight: bold;
        color: #13A2E2;
    }
}