@import url("https://fonts.googleapis.com/css?family=Roboto");

.aboutMe {
    margin-top: 40px;
    background-color: #1B1E21;  
    padding-bottom: 40px;  

    .profile {
        display: flex;
        flex-direction: column;
        align-items: center;
    
        img {
            padding: 20px;
            border-radius: 50%;    
        }
     
        .heading h2 {
            padding-top: 40px;
            text-align: center;
            font-weight: bold;
            color: #13A2E2;
        }
    
        .description {
            text-align: justify;
            // margin-left: 20px;
            // margin-right: 20px;
            margin: 0 auto;
            font-size: 20px;
            font-family: 'Roboto', 'sans-serif';
            color:#d8d8d8;
            width: 75%;
        }
    }

    .contacts{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 20px;
        color:#d8d8d8; 

        .social-links {
            //width: 20%;
            display:flex;
            justify-content: left;
            column-gap: 15px;
        }

        .contact {
            padding-top: 20px;
            display:flex;
            flex-direction: column;
            row-gap: 15px;
            font-size: 20px;
            font-family: 'Roboto', 'sans-serif';
        }

    }
}