.project-overlay-card {
    display: flex;
    color: inherit;


    h5 {
      color: #f2f2f2;
      font-size: 24px;
      text-align: center;   
    }

    h6 {
      color: #D8D8D8;
      font-size: 18px;
      text-align: center;   
    }

    .project-card-body {
      display: flex;
      position: relative;
      align-items: center;
      justify-content: center;
      background-color: rgba(#1B1E21, 1);
      border-width: 0px;
      border-color: #13A2E2;
      border-style: solid;
      border-radius: 10px;
      width: 100%;
      height: auto;
      aspect-ratio: 16 / 9;
    
      .project-card-image {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        border-radius: 10px;

        .card-img {
          height: 100%;
          width: auto;
          max-width: 100%;
        }
      }
    
      .project-card-text {
        background-color: rgba(#0f0f0f, 1);
        display: block;
        text-align: center;
        position: absolute;
        bottom: 0;
        width: 100%;
        padding: 5px;
        opacity: 0;
        transition: 0.3s;
      }
    }
}

.project-overlay-card:hover .project-card-text {
  opacity: 1;
}

.project-overlay-card:hover .project-card-body{
  border-width: 2.5px;
}